import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { EditBannerPageComponent } from './edit-banner-page.component';

const routes: Routes = [
  { path: '', component: EditBannerPageComponent }
];

@NgModule({
  imports: [
    RouterModule.forChild(routes)
  ],
  exports: [
    RouterModule
  ]
})
export class EditBannerPageRoutingModule { }
