import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';

import { BannerService } from '../../angular-services/banner.service';
import { Banner } from '../../classes/banner';

@Component({
  selector: 'app-edit-banner-form-admin',
  templateUrl: './edit-banner-form.component.html',
  styleUrls: ['./edit-banner-form.component.css']
})
export class EditBannerFormComponent implements OnInit {

  @Output() bannerTitle = new EventEmitter<string>();
  mediaType = 'banners';
  urlObjects: string = 'https://viruscursinho.com.br:3002/banners';
  showModalSelectFile: boolean = false;
  tabIndex: number = 1;
  objects: any[] = [];
  sizePage: number = 8;
  pageSelected: number = 0;
  pages: any[] = [];
  banner: Banner = new Banner();
  formEditBanner: FormGroup;
  alert: any = { type: '', message: '', show: false };

  constructor(private bannerService: BannerService, private route: ActivatedRoute) { }

  ngOnInit() {
    this.initForm();
    this.getBanner();
  }

  initForm() {
    this.formEditBanner = new FormGroup({
      title: new FormControl('', Validators.required),
      description: new FormControl(''),
      photo: new FormControl(''),
      url: new FormControl(''),
      status: new FormControl('', Validators.required)
    });
  }

  getBanner() {
    let key = this.route.snapshot.paramMap.get('id');
    this.bannerService.getBanner(key).subscribe(
      response => {
        if (response !== undefined) {
          this.banner = response;
          this.bannerTitle.emit(this.banner.title);
          this.loadForm();
        } else {
          this.alert.type = 'danger';
          this.alert.message = 'Houve algum erro ao salvar os dados!';
          this.alert.show = true;
        }
      }
    )
  }

  loadForm() {
    this.formEditBanner.patchValue({
      title: this.banner.title,
      description: this.banner.description,
      photo: this.banner.photo,
      url: this.banner.url,
      status: this.banner.status
    });
  }

  save() {
    if (this.formEditBanner.valid) {
      let key = this.route.snapshot.paramMap.get('id');
      let data = this.formEditBanner.value;
      if (data.status === "true") {
        data.status = true;
      } else {
        data.status = false;
      }
      this.bannerService.updateBanner(key, data).subscribe(
        response => {
          if (response !== undefined) {
            this.alert.type = 'success';
            this.alert.message = 'Dados salvos com sucesso!';
            this.alert.show = true;
          } else {
            this.alert.type = 'danger';
            this.alert.message = 'Houve algum erro ao salvar os dados!';
            this.alert.show = true;
          }
        }
      )
    }
  }

  changeStatusModalSelectFile() {
    if (this.showModalSelectFile) {
      this.showModalSelectFile = false;
    } else {
      this.showModalSelectFile = true;
    }
  }

  setUrlObject(link) {
    this.formEditBanner.patchValue({
      photo: link
    });
    this.showModalSelectFile = false;
  }

}
